.list-wrap {
  width: 100%;
  margin: 0 auto;
}
.list-wrap .title-wrap {
  margin-bottom: 0.46875rem;
}
.list-wrap .row .item {
  background-color: #fff;
  margin-bottom: 0.46875rem;
  margin-top: 0.46875rem;
  overflow: hidden;
  border: 0.03125rem solid #fff;
  min-height: 17.5rem;
}
.list-wrap .row .item .img-box {
  overflow: hidden;
  padding: 0.3125rem;
  min-height: 11.4375rem;
}
.list-wrap .row .item .img-box img {
  overflow: hidden;
}
.list-wrap .row .item .title {
  padding-top: 0.3125rem;
  padding-left: 0.46875rem;
  padding-right: 0.46875rem;
  height: 3.4375rem;
  overflow: hidden;
  color: #333;
  text-decoration: none;
}
.list-wrap .row .item .title:hover {
  text-decoration: underline;
  color: #F6912D !important;
}
.list-wrap .row .item .price {
  padding-top: 0;
  padding-bottom: 0.3125rem;
  padding-left: 0.3125rem;
  padding-right: 0.3125rem;
  line-height: 2.34375rem;
  height: 2.34375rem;
  overflow: hidden;
  vertical-align: bottom;
}
.list-wrap .row .item .price i.unit {
  color: #EE4D2D;
  font-size: 0.75rem;
  padding-right: 0.15625rem;
  display: inline-block;
  overflow: hidden;
  height: 0.75rem;
  line-height: 0.75rem;
  vertical-align: sub;
}
.list-wrap .row .item .price span.value {
  color: #EE4D2D;
  font-size: 1.5rem;
  vertical-align: bottom;
}
.list-wrap .row .item .price span.original-value {
  color: #EE4D2D;
  vertical-align: bottom;
  background-color: #FFF5E2;
  padding: 0.0625rem 0.15625rem;
  margin-left: 0.625rem;
  padding-right: 0.3125rem;
}
.list-wrap .row .item .price span.del-value {
  text-decoration: line-through;
  color: #999;
  vertical-align: bottom;
  display: inline-block;
  overflow: hidden;
  height: 0.75rem;
  line-height: 0.75rem;
  vertical-align: sub;
  margin-left: 0.46875rem;
}
.list-wrap .item:hover {
  border: 0.03125rem solid #F6912D;
}
.list-wrap .more-wrap {
  clear: both;
  font-size: 1rem;
  font-weight: 12.5rem;
  height: 2.5rem;
  line-height: 1.5625rem;
  color: #F6912D;
  text-align: center;
  display: block;
}
.list-wrap .more-wrap a {
  color: #F6912D;
}
